<template>
    <div>
        <Row> <!-- v-permission.show="'m:product:add'"-->
            <Button type="primary"  v-permission.show="'m:product:add'" @click="add" >
            <Icon type="plus"></Icon>新增产品</Button>
        </Row>
        <Row class="margin-top-10">
            <label style="margin-right:8px;">主产品名称:</label>
            <Input v-model="table.query.name" placeholder="" clearable style="width: 130px" />
            <label class="margin-left-10" style="margin-right:8px;">产品编号:</label>
            <Input v-model="table.query.outId" placeholder="" clearable style="width: 130px" />
            <label class="margin-left-10" style="margin-right:8px;">所属资源商:</label>
            <Select clearable v-model="table.query.epId" style="width: 150px" >
                <Option v-for="item in ep" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <Table :columns="table.col" :data="table.data"></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float:right;">
                        <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>
        <Modal v-model="model" :mask-closable="false" :title="modelTitle" :loading="loadingBtn" @on-ok="addOrUpdate" @on-cancel="cancel">
            <div style="height: 400px">
                <Form ref="form" :model="vo" :label-width="100" :rules="validate">
                    <Form-item label="主产品名称" prop="name">
                        <Input v-model.trim="vo.name" :maxlength="50" />
                    </Form-item>
                    <Form-item label="主产品编号" prop="outId">
                        <Input v-model.trim="vo.outId" :maxlength="20" />
                    </Form-item>
                    <FormItem label="所属资源商" prop="epId" >
                        <Select transfer v-model="vo.epId" >
                            <Option v-for="item in ep" :value="item.id" :key="item.id">{{ item.name }}</Option>
                        </Select>
                    </FormItem>
                </Form>
            </div>
        </Modal>
    </div>
</template>
<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
import { EpType } from '@/libs/dic';
export default {
    name: 'Product',
    mixins: [ TableColRender, TableDataView, ModelView ],
    data () {
        return {
            EpType,
            table: {
                col: [{
                    title: '产品编号',
                    key: 'outId'
                }, {
                    title: '产品名称',
                    key: 'name'
                }, {
                    title: '所属资源商',
                    key: 'epName'
                }, {
                    title: '资源商ID',
                    key: 'epId'
                }, {
                    title: '创建时间',
                    key: 'createTime',
                    render: this.renderDate
                }, /* {
                    title: '更新时间',
                    key: 'updateTime',
                    render: this.renderDate
                }, */ {
                    title: '操作',
                    key: 'action',
                    width: 400,
                    align: 'center',
                    render: (h, params) => this.renderActions(h, params, [{
                        config: { loading: this.loadingBtn, style: { marginRight: '5px' } },
                        click: () => this.$router.push({
                            name: 'SubProductList',
                            query: { id: params.row.id, name: params.row.name }
                        }),
                        text: '查询子产品'
                    }, {
                        config: { loading: this.loadingBtn, style: { marginRight: '5px' } }, // 暂时未对按钮作权限限制 , disabled: params.row.value === ''
                        click: () => this.$router.push({
                            name: 'AddSubProduct',
                            query: { id: params.row.id, name: params.row.name }
                        }),
                        text: '新增子产品'
                    }, {
                        config: { loading: this.loadingBtn, style: { marginRight: '5px' } }, // display:(params.row.EpType==null)?"none":"inline-block"
                        click: () => this.update(params.row),
                        text: '修改'
                    }, {
                        type: 'delete',
                        click: () => this.remove(params.row)
                    }])
                }],
                url: '/api/nmedia/product/',
                query: {
                    name: null,
                    outId: null,
                    epId: null
                }
            },
            vo: {
                id: null,
                outId: null,
                name: null,
                epName: null,
                epId: null,
                createTime: new Date()
            },
            validate: {
                outId: [{ required: true, trigger: 'blur', message: '产品编号不能为空' }],
                name: [{ required: true, trigger: 'blur', message: '产品名称不能为空' }],
                epId: [{ required: true, type: 'number', trigger: 'change', message: '请选择资源商类型' }]
            },
            ep: [],
            addUrl: 'api/nmedia/product/',
            updateUrl: 'api/nmedia/product/update',
            removeUrl: 'api/nmedia/product'
        };
    },
    computed: {
        isUpdate () {
            return this.vo.id !== null && this.vo.id !== undefined;
        },
        generateAddOrUpdate () {
            return { ...this.vo };
        }
    },
    async created () {
        await this.loadAllResource();
    },
    methods: {
        async add () {
            this.$super(ModelView).add();
            this.vo.id = null;
        },
        async update (item) {
            this.loadingBtn = true;
            Object.keys(this.vo).forEach(key => this.vo[key] = item[key]);
            this.model = true;
        },
        // 加载所有资源商
        async loadAllResource () {
            const result = await this.get('api/ep/list', { type: this.roleType });
            if (result.success) {
                this.ep = result.value;
            }
        }
    }
};
</script>
